body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container-home {
  text-align: center;
  font-family: fantasy;
  font-size: 40px;
  margin-bottom: 20px;
}

.container-title {
  text-decoration: none;
  color: black;
}


.my-button {
  color: white;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-weight: 500;
  border: 1px solid transparent;
  padding: .5rem .75rem;
  font-size: 1.2rem;
  line-height: 1.5;
  border-radius: .25rem;
  min-width: 200px;
  max-width: 400px;
}

.button-translate {
  background-color: #2F56A6;
}

.button-translate:hover {
  background-color: #23417D;
}
.list-table {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 95%;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
}

.list-table td {
  border: 1px solid #ddd;
  padding: 8px;
  font-family: Lato-Regular;
  font-size: 15px;
  color: #808080;
  line-height: 1.4;
  width: 80px;
}

.list-table tr:nth-child(even){
  background-color: #ccc;
}

.table-tr:hover {
  background-color: #ececff !important;
  cursor: pointer;
}

.table-headers {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #2F56A6;
  font-family: Lato-Bold;
  color: #fff;
  line-height: 1.4;
  white-space: nowrap;
}


.welcome-message {
  font-size: 24px;
  line-height: 30px;
  letter-spacing: .4px;
}

.note {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

.table-header {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  text-align: center;
}

.wide-column {
  width: 40%;
}
.narrow-column {
  width: 10%;
}
.translate-page-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 80%;
    margin-left:auto;
    margin-right: auto;
}

.translate-text-area-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    height: 100%;
    width: 100%;
    margin-bottom: 8px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.input-text-area {
    cursor: text;
}

.translated-text-area {
    cursor: pointer;
    background: lightgray;
}

/* .input-text-result {
    background: lightgray;
} */

.text-area {
    line-height: 32px;
    font-size: 24px;

    border-radius: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    resize: auto;
    white-space: pre-wrap;
    overflow-wrap: break-word;
    border-width: 1px;
    border-style: solid;
    border-color: currentColor;
    border-color: initial;
    border-image: none;
    border-image: initial;
    padding: 2px;
    
    color: #333;
    font-family: 'Roboto',arial,sans-serif;
    padding-bottom: 2px;
    padding-left: 16px;
    padding-right: 90px;
    resize: none;
    width: 100%;
    height: 100%;
    min-height: 300px;
    overflow: visible;
    margin-left: 10px;
    margin-right: 10px;
}

.translate-btn {
    display:inline-block;
}



.button-container {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}

.link-text {
    text-decoration: none;
    color: white !important;
    text-align: center;
    display: inline-block
}
.info-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom: 20px;
    margin-left: 5%;
}

.label {
    color: rgba(0, 0, 0, 0.54);
    padding: 0;
    font-size: 1rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    line-height: 1;
}

.text-box {
    text-rendering: auto;
    color: #000;
    color: initial;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    display: inline-block;
    text-align: start;
    margin: 0em;
    font-size: 20px;
    border: none;
}
